'use client';

import { useState, useEffect, ReactNode } from 'react';
import { Box, CircularProgress } from '@mui/material';
import { isInLiffEnvironment, waitForLiffReady } from '../utils/liff';

interface LiffSafeWrapperProps {
  children: ReactNode;
  fallback?: ReactNode;
}

export default function LiffSafeWrapper({ children, fallback }: LiffSafeWrapperProps) {
  const [isLiffReady, setIsLiffReady] = useState(false);
  const [isClient, setIsClient] = useState(false);

  useEffect(() => {
    // Check if we're in client-side
    setIsClient(true);

    // Check if we're in LIFF environment
    const isInLiff = typeof window !== 'undefined' && (
      window.location.href.includes('liff://') ||
      window.navigator.userAgent.includes('Line') ||
      window.location.hostname.includes('liff')
    );

    if (isInLiff) {
      // LIFF environment - wait for LIFF to be ready
      const checkLiffReady = () => {
        if (typeof window !== 'undefined' && window.liff) {
          if (window.liff.isLoggedIn && window.liff.isLoggedIn()) {
            setIsLiffReady(true);
          } else {
            // Wait a bit more for LIFF to initialize
            setTimeout(checkLiffReady, 100);
          }
        } else {
          // LIFF not loaded yet, wait
          setTimeout(checkLiffReady, 100);
        }
      };

      // Add a delay to ensure LIFF is fully loaded
      setTimeout(() => {
        checkLiffReady();
      }, 500);
    } else {
      // Normal browser - ready immediately after client mount
      setIsLiffReady(true);
    }
  }, []);

  // Show loading while waiting for client-side hydration or LIFF ready
  if (!isClient || !isLiffReady) {
    return (
      fallback || (
        <Box sx={{ 
          height: '100vh',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          background: `
            linear-gradient(135deg, 
              rgba(16, 185, 129, 0.08) 0%, 
              rgba(5, 150, 105, 0.05) 25%,
              rgba(52, 211, 153, 0.03) 50%,
              rgba(255, 255, 255, 0.95) 75%,
              rgba(248, 250, 252, 1) 100%
            )
          `
        }}>
          <CircularProgress size={60} sx={{ color: '#10b981' }} />
        </Box>
      )
    );
  }

  return <>{children}</>;
}

// Type declaration for LIFF
declare global {
  interface Window {
    liff?: {
      isLoggedIn: () => boolean;
      init: (config: any) => Promise<void>;
      ready: Promise<void>;
      [key: string]: any;
    };
  }
} 